/* eslint no-undef: 0 */
/* eslint arrow-parens: 0 */
import React from 'react';
import { enquireScreen } from 'enquire-js';

import Nav0 from './Nav0';
import Banner0 from './Banner0';
import Feature1 from './Feature1';
import Feature2 from './Feature2';
import Content9 from './Content9';
import Feature0 from './Feature0';
import Feature5 from './Feature5';
import Content11 from './Content11';
import Footer1 from './Footer1';
/* import Footer2 from './Footer2'; */

import {
  Nav01DataSource,
  Banner01DataSource,
  Feature10DataSource,
  Feature20DataSource,
  Content90DataSource,
  Feature01DataSource,
  Feature50DataSource,
  Content111DataSource,
  Footer11DataSource,
 /* Footer20DataSource, */
} from './data.source';
import './less/antMotionStyle.less';

let isMobile;
enquireScreen((b) => {
  isMobile = b;
});

const { location = {} } = typeof window !== 'undefined' ? window : {};

export default class Home extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isMobile,
      show: !location.port, //  dva 2.0 
    };
  }

  componentDidMount() {
    // ;
    enquireScreen((b) => {
      this.setState({ isMobile: !!b });
    });
    // dva 2.0 
    /*  dva 2.0  start */
    if (location.port) {
      //  build  200-300ms ;
      setTimeout(() => {
        this.setState({
          show: true,
        });
      }, 500);
    }
    /*  dva 2.0  end */
  }

  render() {
    const children = [
      <Nav0
        id="Nav0_1"
        key="Nav0_1"
        dataSource={Nav01DataSource}
        isMobile={this.state.isMobile}
      />,
      <Banner0
        id="Banner0_1"
        key="Banner0_1"
        dataSource={Banner01DataSource}
        isMobile={this.state.isMobile}
      />,
      <Feature1
        id="Feature1_0"
        key="Feature1_0"
        dataSource={Feature10DataSource}
        isMobile={this.state.isMobile}
      />,
      <Feature2
        id="Feature2_0"
        key="Feature2_0"
        dataSource={Feature20DataSource}
        isMobile={this.state.isMobile}
      />,
      <Content9
        id="Content9_0"
        key="Content9_0"
        dataSource={Content90DataSource}
        isMobile={this.state.isMobile}
      />,
      <Feature0
        id="Feature0_1"
        key="Feature0_1"
        dataSource={Feature01DataSource}
        isMobile={this.state.isMobile}
      />,
      <Feature5
        id="Feature5_0"
        key="Feature5_0"
        dataSource={Feature50DataSource}
        isMobile={this.state.isMobile}
      />,
      <Content11
        id="Content11_1"
        key="Content11_1"
        dataSource={Content111DataSource}
        isMobile={this.state.isMobile}
      />,
      <Footer1
        id="Footer1_1"
        key="Footer1_1"
        dataSource={Footer11DataSource}
        isMobile={this.state.isMobile}
      />,
     // <Footer2
     //   id="Footer2_0"
     //   key="Footer2_0"
     //   dataSource={Footer20DataSource}
     //   isMobile={this.state.isMobile}
     // />,
    ];
    return (
      <div
        className="templates-wrapper"
        ref={(d) => {
          this.dom = d;
        }}
      >
        {/*  dva 2.0  {children} start */}
        {this.state.show && children}
        {/*  dva 2.0  {children} end */}
      </div>
    );
  }
}
